export default {
	name:"trailerStars",
	
	data() {
		return {
			yellowScroe:3,
			grayScroe:2
		};
	},
	props:{
		innerScroe:0, //定义外部传入的分数
		showNum:0,	//是否需要具体的分数 1：显示   0： 不显示
	},
	// 生命周期组件创建完成后被调用
	created() {
		// console.log('this.innerScroe=' + this.innerScroe)
		var tempScroe = 0;
		if (this.innerScroe != null && this.innerScroe != undefined && this.innerScroe != '' ) {
			tempScroe = this.innerScroe;
		} 
		 var yellowScroe = parseInt(tempScroe /2);
		 var grayScroe = 5 - yellowScroe;
		 this.yellowScroe = yellowScroe;
		 this.grayScroe = grayScroe;
	}
}
